import React, { Component } from "react";
import SEO from "../../components/seo";
import openPositions from "../../components/Careers/opl.json";
import { getSlug } from "../../components/Common/utils/getSlug.js";
import { Link } from "react-router-dom";

class LightningTalks extends Component {
  render() {
    return (
      <section className="first-section">
        <SEO
          isDynamic={false}
          title="Gojek: #FirstPrinciples - Lightning Talks"
          description="#FirstPrinciples is a series of Product webinars from folks who have been there, done that. In the world of Product Management, First Principles is not alien. #FirstPrinciples aims to get some of the most accomplished Product Leaders from the ecosystem to share their learnings on building great products."
          url="https://gojek.io"
        />
        <div className="container-fluid">
          <div className="row align-items-center bg-green">
            <div className="col-md-5 p-md-0 p-5">
              <h1 className="maison-bold text-center text-white">
                #FirstPrinciples - Lightning Talks
              </h1>
            </div>
            <div className="col-md-7 p-0">
              <div className="bg-dark  event-banner go-jek-work-gradient background-img relative-position" />
            </div>
          </div>
        </div>

        <div className="container">
          <h2 className="maison-bold mt-5 mb-3">What is #FirstPrinciples?</h2>
          <p className="description-font">
            <strong className="text-green">
              <em>#FirstPrinciples</em>
            </strong>{" "}
            is a series of Product webinars from folks who have been there, done
            that. In the world of Product Management, First Principles is not
            alien. #FirstPrinciples aims to get some of the most accomplished
            Product Leaders from the ecosystem to share their learnings on
            building great products.
          </p>

          <p className="description-font text-dark">
            Our mission is to build a platform to share ideas, learnings, and
            mistakes - a way for all of us to learn in shaping our own product
            journey.
          </p>

          <h2 className="maison-bold mt-5 mb-4">What are Lightning Talks?</h2>
          <p className="description-font text-dark">
            While thinking of #FirstPrinciples, we realized a star cast draws
            crowds, but we also want some folks in the trenches to share their
            learnings.
          </p>

          <p className="description-font text-dark">
            With Lightning talks, we are opening three talk slots of five
            minutes each before the guest speaker begins their presentation.
          </p>

          <p className="description-font text-dark">
            If you haven’t spoken on a public forum before, here’s your chance
            to do so, virtually. This can help you build confidence in the
            comfort of your home, and fine-tune your topic for a larger talk
            later. Experienced folks can also share their interesting ideas
            through this.
          </p>

          <p className="description-font text-dark">
            We hope this initiative will also help build the larger community.
            Additionally, it also helps us build our vision of building
            #FirstPrinciples into a strong platform.
          </p>

          <h2 className="maison-bold mt-5 mb-4">
            Ok, I’m in, how does it work?
          </h2>
          <p className="description-font text-dark">
            Once your talk is picked, a mentor will be assigned to help you
            fine-tune your talk. These mentors are veterans from the industry
            who have been there, done that. They will help you with crafting
            your presentation to make it clear and engaging.
          </p>

          <p className="description-font text-dark">
            There won’t be any Q&amp;As for Lightning talks and you can leave
            your twitter ID for people to get in touch with you.
          </p>

        </div>
        <div className="bg-green p-5 p-md-3 p-lg-5">
          <div className="container-fluid">
            <div className="row justify-content-center">
              <div className="col-lg-12">
                <h3 className="px-2 text-center font-xl-l maison-extrabold text-white">
                  How to apply?
                </h3>
                <p className="text-white text-center">
                  You can fill in the registration from here
                </p>
                <div className="text-center pt-3">
                  <a
                    href="https://docs.google.com/forms/d/e/1FAIpQLSconqSuIkM77XRPBnHSmf1TjtQT6oqu1ek5LxZoNfj23opaNQ/viewform?usp=send_form"
                    target="_blank"
                    className="btn custom-btn text-center maison-bold text-uppercase font-md bg-white text-green challenging-button"
                  >
                    Apply Now
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default LightningTalks;



